.education_pack {
    display: grid;
    margin: 140px auto 0;
    width: 1046px;
}
.education_pack_top {
    width: 500px;
    display: grid;
    justify-self: center;
    height: 200px;
    ._title_ {
        text-align: center;
        justify-self: center;
        margin: 0 0 40px 0;
        font-size: 24px;
        line-height: 34px;
        .line {
            left: 40px;
            top: 15px;
        }
    }
}
.education_pack_change,
.education_pack_change_time {
    justify-self: center;
}
.education_pack_change_time {
    margin-top: 30px;
}

.label_input {
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #000000;
    position: relative;
}
.label_input::after {
    content: "";
    display: block;
    position: absolute;
    width: 120%;
    background: #94c6c4;
    height: 2px;
    left: -10%;
}
.e_pack {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
    > input:checked + label::after {
        background: #236967;
    }
    > label:nth-child(2) {
        justify-self: end;
    }
    > label:nth-child(4) {
        justify-self: start;
        margin-left: 50px;
    }
    input {
        display: none;
    }
}
.education_pack_bottom {
    min-width: 0;
    margin: 40px auto 0 !important;
    > div {
        display: none;
        grid-template-columns: repeat(3, 1fr);
    }
    .e_pack_active {
        display: grid;
    }
}
.education_pack_bottom {
    // max-width: 1050px;
    width: 100%;
}
.e_pack_active {
    display: grid;
}
.education_pack_block {
    background: #fffbed;
    border-radius: 30px;
    display: grid;
    text-align: center;
    width: 300px;
    padding-bottom: 60px;
    .button {
        justify-self: center;
    }
    margin-bottom: 140px;
}

@media all and (max-width: 420px) {
    .form {
        margin: 100px auto;
    }
    .education_pack {
        width: 100%;
    }
    .education_pack_top,
    .education_pack_bottom {
        width: 100%;
    }
    .education_pack_top {
        ._title_ {
            .line {
                width: 260px;
                top: 15px !important;
                left: 50px;
            }
        }
    }
    .e_pack {
        label {
            font-size: 16px;
            line-height: 24px;
            text-align: center;
            padding-bottom: 15px;
            box-sizing: border-box;
            display: inline-block;
        }
        > label:nth-child(4) {
            margin-left: 20px;
        }
        > label:nth-child(2) {
            height: 26px;
            overflow: hidden;
        }
        > label:nth-child(2)::after {
            top: 25px;
        }
        label::after {
            width: 110%;
            left: -10%;
        }
    }
    .education_pack_bottom {
        > div {
            max-width: 325px !important;
            flex-wrap: nowrap;
        }
    }
    .education_pack_block {
        width: 100%;
        // width: 300px !important;
    }
    .swiper-slide {
        max-width: 300px !important;
    }
    .e_pack_active {
        display: flex !important;
    }

    .education_pack_block {
        margin-left: 0px;
    }
}
