@media all and (min-width: 1000px) and (max-width: 1250px) {
    .wrapper {
        width: 100%;
    }
    .container {
        width: 100%;
    }
    .header_container {
        width: 100%;
        grid-template-columns: repeat(3, 1fr);
    }
    .nav {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .main_title {
        .container {
            display: grid;
            align-items: center;
        }
    }
    .main_title_text_content {
        p {
            font-size: 30px;
            line-height: 50px;
        }
    }
    .for_children {
        justify-items: center;
        ._content {
            justify-self: center;
        }
    }
    .why_school {
        width: 100%;
    }
    .right_side_left_blocks {
        justify-self: center;
    }
    .form {
        width: 100%;
    }
    .eductaion_for {
        width: 100%;
    }
    .example {
        max-width: 874px;
        margin: 0 auto;
    }
    .example_right_side {
        justify-self: end;
    }
    .history {
        max-width: 847px;
        margin: 0 auto;
    }
    .footer {
        width: 100%;
    }
    .footer_right_side {
        justify-self: center;
        margin-left: 50px;
    }
    .footer_right_side a {
        max-width: 100%;
        justify-self: start;
        text-align: left !important;
    }
    .footer_title {
        text-align: left !important;
    }
    .footer_foot {
        > a:nth-child(2) {
            margin-right: 50px;
        }
    }
    .change_for_children_columns {
        grid-template-columns: repeat(2, 1fr) !important;
    }
    .change_for_children_column {
        width: 100%;
    }
    .after_red {
        grid-column: 2/3;
    }
    .chiidren_offline_come {
        display: grid;
        ._title_ {
            justify-self: center;
        }
    }
    .chiidren_offline_come_blocks {
        justify-items: center;
    }
    .education_pack {
        width: 100% !important;
    }
    .education_pack_block {
        width: 100% !important;
    }
    .education_pack_bottom {
        width: 100%;
    }
}
