.main {
    > .container {
        margin-top: 70px;
    }
}
.change_for_children {
    margin-top: 140px;
    .left_side_title {
        width: 422px;
    }
}
.change_for_children_columns {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    > p {
        font-weight: 300;
        font-size: 16px;
        line-height: 26px;
        letter-spacing: 0.02em;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #363636;
        position: relative;
    }
    > p:nth-child(1) {
        span {
            display: inline-block;
            width: 428px;
        }
    }
    > p:nth-child(2) {
        justify-self: center;
        span {
            display: inline-block;
            width: 255px;
        }
    }
    > p:nth-child(3) {
        span {
            display: inline-block;
            width: 361px;
        }
    }
}
.after::after {
    content: "";
    display: inline-block;
    width: 6px;
    height: 78px;
    background: #fdd503;
    position: absolute;
    left: -14px;
}
.after_red::after {
    background: #fe7167;
}
@media all and (max-width: 420px) {
    .change_for_children {
        .left_side_title {
            margin-top: 100px;
            width: 192px;
            height: 56px;
        }
        .line {
            // margin-top: -20px;
            top: 15px;
            position: absolute;
        }
        .line_none {
            display: block;
            position: absolute;
            top: 40px;
            width: 190px;
            height: 10px;
            background: #ffeca0;
        }
    }
    .change_for_children_columns {
        grid-template-columns: repeat(1, 1fr);
        > p {
            font-size: 14px;
            line-height: 22px;
        }
        > p:nth-child(1) {
            font-size: 16px;
            line-height: 24px;
            font-weight: 500;
            span {
                width: 323px;
            }
        }
        > p:nth-child(2) {
            justify-self: left;
            span {
                width: 255px;
            }
        }
        > p:nth-child(3) {
            span {
                width: 317px;
            }
        }
    }
    .after::after {
        width: 4px;
        height: 60px;
        top: 2px;
    }
    .children_online {
        .children_container {
            margin-left: 20px;
        }
        .results {
            .children_container {
                > ._title_ {
                    max-width: 240px !important;
                    justify-self: left;
                }
            }
        }
    }
}

.learn_language {
    margin-top: 140px;
    background: #fffbed;
    border-radius: 70px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    height: 420px;
    z-index: -2;
    justify-items: center;
    img {
        justify-self: start;
    }
}
._title_ {
    position: relative;
    font-family: "NEXT ART";
    font-style: normal;
    font-weight: 900;
    font-size: 30px;
    line-height: 44px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #000000;
    width: 333px;
    z-index: 2;
    .line {
        position: absolute;
        top: 70px;
        left: 0;
        z-index: -1;
        width: 100%;
    }
    > .line:nth-child(2) {
        top: 20px;
        width: 260px;
    }
}

@media all and (max-width: 420px) {
    .learn_language {
        width: 100%;
        margin-top: 100px;
        grid-template-columns: repeat(1, 1fr);
        img {
            justify-self: center;
        }
    }
    .learn_language_left_side {
        width: 224px;
    }
    ._title_ {
        width: 100%;
        font-size: 20px;
        line-height: 28px;
        .line:nth-child(2) {
            top: 10px !important;
            width: 160px;
        }
        > .line:nth-child(3) {
            top: 40px !important;
        }
    }
}

.results {
    width: 874px;
    margin: 140px auto 0;
    .children_container {
        display: grid;
    }
    ._title_ {
        justify-self: center;
        width: 500px;
        font-size: 24px;
        line-height: 34px;
        margin: 0;
        .line {
            width: 95%;
        }
    }
}

.results_blocks {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
.result_block {
    margin: 40px 25px 40px 0;
    background: #fffbed;
    border-radius: 30px;
    padding: 37px 28px;
    box-sizing: border-box;
}
.result_text {
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
}
@media all and (max-width: 420px) {
    .results {
        width: 100%;
        margin: 100px 0;
        ._title_ {
            margin: 100px 0 0 0;
            max-width: 355px !important;
            span {
                width: 233px;
                display: inline-block;
                font-size: 20px;
                line-height: 28px;
            }
            .line {
                width: 120px !important;
            }
            .line_none {
                display: inline-block;
                width: 233px;
                position: absolute;
                top: 40px;
                z-index: -1;
                left: 0;
            }
        }
    }
    .results_blocks {
        width: 100%;
        grid-template-columns: repeat(1, 1fr);
        margin-left: -20px;
    }
    .line_none {
        height: 10px;
        background: #ffeca0;
    }
    .result_block {
        padding: 37px;
        width: 257px;
        justify-self: center;
    }
    .result_text {
        font-size: 14px;
        line-height: 22px;
        width: 168px;
    }
}
.what_lan {
    width: 1001px;
    margin: 140px auto 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    img {
        margin-left: -20px;
    }
}
.what_lan_right_side {
    grid-column: 2/5;
    display: grid;
    margin-left: 70px;
    grid-template-rows: repeat(3, 1fr);
    ._title_ {
        margin: 0;
        width: 350px;
        > span:nth-child(1) {
            width: 100%;
            display: inline-block;
            font-size: 24px;
            line-height: 34px;
        }
        .line:nth-child(2) {
            top: 15px;
            width: 100%;
        }
        .line:nth-child(3) {
            top: 50px;
            width: 65%;
        }
    }
}
.what_lan_blocks {
    grid-row: 2/4;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
.what_lan_block {
    display: grid;
    margin-left: 10px;
}

@media all and (max-width: 420px) {
    .what_lan {
        width: 100%;
        margin: 100px auto;
        grid-template-columns: repeat(1, 1fr);
        img {
            order: 1;
            margin-top: 60px;
        }
        ._title_ {
            .line:nth-child(2) {
                position: absolute;
                top: 15px !important;
            }
            .line:nth-child(3) {
                position: absolute;
                top: 50px !important;
            }
        }
    }
    .what_lan_right_side {
        grid-column: 1/1;
        order: 0;
        width: 100%;
        margin: 0;
    }
    .what_lan_blocks {
        width: 100%;
        // max-width: 375px;
        grid-template-columns: repeat(2, 1fr);
    }
    .long {
        grid-column: 1/3;
    }
    .what_lan_block {
        margin: 10px 0;
    }
}
.education_pack_bottom {
    display: block !important;
}
