@media all and (max-width: 420px) {
    .eductaion_for {
        width: 100%;
        img {
            width: 100%;
        }
        ._content {
            max-width: 355px;
        }
    }
    .example {
        img {
            width: 100% !important;
        }
    }
    .history {
        .line_none {
            display: inline-block;
        }
    }
    .title_content {
        font-size: 20px;
        line-height: 28px;
    }
    ._content {
        justify-self: center !important;
    }
    .content_rows {
        width: 100%;
        grid-template-columns: repeat(1, 1fr);
    }
    .content_row_title {
        font-size: 16px;
        line-height: 24px;
    }
    .content_row {
        max-width: 355px;
        ul {
            li {
                font-size: 14px;
                line-height: 22px;
            }
        }
    }
    .for_children {
        grid-template-columns: repeat(1, 1fr);
        margin: 100px 0 !important;

        .line {
            width: 120px;
        }
        .line_2 {
            width: 180px;
        }
    }
    .wrapper {
        width: 100% !important;
    }
    .container {
        margin: 0 auto;
    }
    .footer {
        width: 100% !important;
    }
    .smart {
        width: 100%;
        > p,
        > div {
            margin: 0 auto;
        }
        ._content_title {
            height: 85px;
            width: 194px;
            font-size: 20px;
            line-height: 28px;
            margin-left: 20px !important;
            .line {
                width: 100%;
                margin-top: 15px;
            }
            .line_none {
                display: block;
            }
            .line:nth-child(3) {
                width: 90%;
            }
            .line:nth-child(4) {
                width: 70%;
            }
        }
    }
    .b_text {
        grid-template-columns: repeat(3, 1fr);
        margin: 20px 0 80px !important;
    }
    .gray_text {
        font-size: 14px;
        line-height: 22px;
        margin-bottom: -3px;
    }
    .b_letter_block_text {
        font-size: 16px;
        line-height: 24px;
        margin-left: -5px;
    }
    .b_letter {
        font-size: 80px;
        line-height: 28px;
    }
    .smart_text {
        font-size: 14px;
        line-height: 22px;
        max-width: 335px !important;
    }
    .b_letter_block {
        margin-top: 60px;
    }
    .b_letter_block:nth-child(4) {
        grid-column: 1/3;
        justify-self: center;
    }
    .b_letter_block:nth-child(5) {
        margin-left: -50px;
        grid-column: 3/3;
    }
    .b_text {
        margin-bottom: 50px !important;
    }

    .form_left_side_container {
        margin: 65px auto 0 !important;
    }
    body {
        font-family: "Raleway";
    }
    .main_title {
        flex-direction: row;
        flex-wrap: wrap;
        p {
            line-height: 40px;
            font-size: 30px;
        }
    }
    .button {
        padding: 16px 38px;
        width: 213px;
        font-size: 16px;
        margin-bottom: 60px;
    }
    .main_img {
        width: 100%;
    }
    .container {
        // margin: 0 auto;
        width: 100% !important;
    }
    .eductaion {
        display: grid !important;
        grid-template-columns: repeat(1, 1fr);
        justify-content: center;
        justify-items: center;
        margin-top: 80px;
    }
    .eductaion_circle {
        margin-top: 60px;
    }
    .languages_container {
        width: 210px;
    }
    .languages_title {
        font-size: 20px;
        line-height: 28px;
    }
    .languages {
        width: 100%;
        height: auto;
    }
    .lang {
        flex-direction: row;
        flex-wrap: wrap;
        p {
            font-size: 16px;
            line-height: 24px;
            width: 190px !important;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            span {
                justify-self: start;
            }
        }
    }

    .why_school {
        width: 100%;
        margin-top: 100px;
        display: block;
        .left_side {
            width: 100%;
        }
        .title_content {
            font-size: 20px;
            line-height: 28px;
        }
        .right_side {
            grid-template-columns: repeat(1, 1fr);
            width: 100%;
        }
    }
    .line {
        width: 85%;
    }
    .left_side_text {
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 40px;
    }
    .right_side_block_text {
        font-size: 16px !important;
        line-height: 24px !important;
    }
    .form {
        width: 100%;
        grid-template-columns: repeat(1, 1fr);
    }
    .form_title {
        font-size: 20px !important;
        line-height: 28px !important;
        max-width: 212px;
        justify-self: center;
    }
    .change {
    }
    .change {
        width: 100%;
        margin: 0;
        > label:nth-child(2)::before {
            width: 120px;
            left: 30px;
        }
        label::before {
            width: 180px;
            left: -10px;
        }
        label {
            text-align: center;
        }
    }
    .inputs {
        width: 100% !important;
        input,
        select {
            width: 100%;
        }

        input,
        select,
        input::placeholder,
        select::placeholder {
            font-size: 14px;
            line-height: 22px;
        }
        .label {
            font-size: 14px;
            line-height: 22px;
        }
        .button {
            width: 153px;
            justify-self: center;
            grid-column: 1/3;
        }
        .label::after {
            left: -16px;
        }
        #_name {
            margin-right: 20px;
            width: 158px;
        }
    }
    .form_right_side {
        width: 100%;
        overflow: hidden;
        grid-template-columns: repeat(3, 1fr) !important;
        > svg:nth-child(7),
        svg:nth-child(5) {
            display: none;
        }
        > svg:nth-child(4) {
            // order: 1;
            border-bottom-left-radius: 50%;
        }
        svg {
            margin-left: -15px;
        }
    }
    .history {
        width: 100%;
        .line_none {
            top: -30px;
            padding-right: 50px;
            position: absolute;
        }
        .line {
            width: 80% !important;
            margin-top: 75px !important;
        }
        ._content_title {
            max-width: 226px;
        }
        .time_line_history {
            width: 100%;
        }
        .year_info_block {
            > div {
                width: 100%;
                grid-template-columns: repeat(1, 1fr);
                .info {
                    order: 0;
                    margin-left: 20px;
                    justify-self: start;
                    > p:nth-child(1) {
                        font-size: 20px;
                        line-height: 28px;
                    }
                    > p {
                        font-size: 16px;
                        line-height: 24px;
                    }
                }
                img {
                    order: 1;
                }
            }
        }
    }
    .lang {
        display: grid;
        grid-template-columns: repeat(1, 1fr) !important;
    }
    .right_side_left_blocks {
        width: 100%;
    }
    .education_pack_block {
        margin-right: 150px !important;
    }
    .education_pack_top ._title_ .line {
        left: 20% !important;
        width: 210px !important;
    }
    .education_pack_bottom {
        width: 300px !important;
    }
    .children_offline_pack_blocks {
        width: 300px !important;
    }
    .children_offline_pack {
        .education_pack_block {
            margin-left: 0 !important;
        }
    }
    .what_will_we_do {
        ._title_ {
            .line {
                width: 268px !important;
            }
        }
    }
    .offline_result_blocks {
        justify-items: center !important;
    }
    .offline_result_block {
        width: 266px !important;
        display: flex !important;
    }
    .offline_result_text {
        margin-left: 15px;
    }
    .offline_result_block:nth-child(1) {
        justify-self: center !important;
        .offline_result_text {
            margin-left: 0;
        }
    }
    .offline_result {
        display: grid;
        ._title_ {
            justify-self: center;
        }
    }
    .old_offline_all_task img {
        justify-self: center !important;
    }
}
