.wrapper {
    width: 1920px;
}

.container {
    width: 1260px;
    margin: 0 auto;
}
.footer {
    padding-top: 80px;
    width: 1920px;
    background: #fffbed;
    border-radius: 70px 70px 0px 0px;
    margin: 140px auto 0;
    display: grid;
}
.footer_content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
.footer_left_side {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    a {
        margin-top: 14px;
        font-weight: 300;
        font-size: 16px;
        line-height: 26px;
        /* identical to box height, or 162% */

        letter-spacing: 0.02em;
        font-feature-settings: "pnum" on, "lnum" on;

        color: #363636;
    }
}
.footer_title {
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #000000;
    margin: 0 0 17px 0;
}
.footer_center {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    a {
        justify-self: center;
    }
    .button {
        grid-column: 1/5;
        justify-self: center;
        border: none;
    }
}
.logo {
    grid-column: 1/5;
}
.footer_right_side {
    justify-self: end;
    display: grid;
    grid-template-columns: repeat(1, 1fr);

    justify-content: end;
    .footer_title {
        text-align: end;
    }
    a {
        display: inline-block;
        max-width: 164px;
        justify-self: end;
        font-weight: 300;
        font-size: 16px;
        line-height: 26px;
        letter-spacing: 0.02em;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #363636;
    }
    .footer_address {
        text-align: end;
    }
}
.footer_foot {
    display: grid;
    margin-top: 60px;
    grid-template-columns: repeat(2, 1fr);
    > a:nth-child(2) {
        justify-self: end;
    }
    font-size: 14px;
    line-height: 26px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
}
