.old_offline_mini_content {
    font-family: "Raleway" !important;
    font-style: normal !important;
    font-weight: 500 !important;
    font-size: 22px !important;
    line-height: 24px !important;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636 !important;
    margin-bottom: 40px;
}
.old_offline_main {
    .eductaion {
        margin-top: 100px;
        margin: 100px 0 140px 0;
    }
}
.purpose {
    margin: 140px auto 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    ._title_ {
        width: 407px;
        font-size: 24px;
        line-height: 34px;
        .line:nth-child(2) {
            width: 100%;
            top: 15px;
        }
        .line:nth-child(3) {
            width: 340px;
            top: 50px;
        }
    }
}
.purpose_text {
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
    width: 455px;
}
.what_will_we_do {
    margin-top: 140px;
    ._title_ {
        text-align: center;
        width: 100%;
        .line {
            left: 420px;
            width: 400px;
        }
    }
}
.what_will_we_do_blocks {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-content: center;
    justify-items: center;
}
.what_will_we_do_text {
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
}
.b_num {
    width: 70px;
    height: 70px;
    display: grid;
    align-items: center;
    font-family: "NEXT ART";
    font-style: normal;
    font-weight: 900;
    font-size: 40px;
    line-height: 34px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #ffffff;
    text-align: center;
    border-radius: 50%;
    margin: 0 auto;
}
.green {
    background: #236967;
}
.yellow {
    background: #ffcc1a;
}
.orange {
    background: #fc8b4c;
}
.red {
    background: #fe7167;
}
.what_will_we_do_text_1 {
    width: 147px;
}
.what_will_we_do_text_2 {
    width: 154px;
}
.what_will_we_do_text_3 {
    width: 119px;
}
.what_will_we_do_text_4 {
    width: 161px;
}
.offline_result {
    margin: 140px auto 0;
    ._title_ {
        width: 76px !important;
        font-size: 24px;
        line-height: 34px;
        .line {
            width: 95%;
            top: 15px;
        }
    }
}
.offline_result_blocks {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-items: end;
}
.offline_result_block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    img {
    }
}
.offline_result_text {
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
}
.offline_result_block:nth-child(1) {
    width: 302px;
    justify-self: start;
    p {
        grid-column: 1/3;
    }
}
.offline_result_block:nth-child(2) {
    width: 168px;
}
.offline_result_block:nth-child(3) {
    width: 190px;
}
.offline_result_block:nth-child(4) {
    width: 237px;
}
.old_offline_all_task {
    margin: 140px auto 0;
    background: #fffbed;
    border-radius: 70px;
    padding: 100px 213px;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    ._title_ {
        font-size: 24px;
        line-height: 34px;
        width: 420px;
        .line:nth-child(2) {
            width: 90%;
            top: 15px;
        }
        .line:nth-child(3) {
            width: 95%;
            top: 50px;
        }
    }
    img {
        justify-self: end;
    }
}
.all_task_text {
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    width: 320px;
    margin: 30px 0 40px 0;
}
@media all and (max-width: 420px) {
    .old_offline_mini_content {
        font-size: 20px;
        line-height: 30px !important;
        width: 246px;
    }
    .old_offline_education_pack {
        display: none !important;
    }
    .old_offline_main {
        .eductaion {
            .eductaion_circle {
                justify-self: left;
                margin-left: 30px;
                p {
                }
            }
        }
    }
    .purpose {
        width: 100%;
        grid-template-columns: repeat(1, 1fr);
        img {
            width: 335px;
            justify-self: center;
        }
    }
    .purpose_right_side {
        width: 100%;
        ._title_ {
            width: 340px;
            > .line:nth-child(3) {
                width: 290px;
            }
        }
    }
    .purpose_text {
        width: 100%;
        font-size: 14px;
        line-height: 22px;
    }
    .what_will_we_do_blocks {
        grid-template-columns: repeat(2, 1fr);
    }
    .what_will_we_do_text {
        font-size: 16px;
        line-height: 24px;
    }
    .what_will_we_do_text_1 {
        width: 123px;
    }
    .what_will_we_do_text_2 {
        width: 133px;
    }
    .what_will_we_do_text_3 {
        width: 112px;
    }
    .what_will_we_do_text_4 {
        width: 151px;
    }
    .offline_result_blocks {
        grid-template-columns: repeat(1, 1fr);
        justify-items: start;
    }
    .offline_result_block {
        p {
            font-size: 16px;
            line-height: 24px;
            font-weight: 500;
        }
    }
    .offline_result {
        margin: 100px auto 0;
    }
    .offline_result_block:nth-child(1) {
        p {
            font-weight: 300;
            font-size: 14px;
            line-height: 22px;
        }
    }
    .offline_result_block:nth-child(2) {
        width: 218px;
        p {
            width: 149px;
        }
    }
    .offline_result_block:nth-child(3) {
        width: 234px;
        p {
            width: 165px;
        }
    }
    .offline_result_block:nth-child(4) {
        width: 266px;
        p {
            width: 200px;
        }
    }
    .old_offline_all_task {
        margin: 100px auto 0;
        padding: 60px 10px 60px 10px;
        grid-template-columns: repeat(1, 1fr);
        ._title_ {
            width: 100%;
        }
    }
    .all_task_text {
        font-size: 14px;
        line-height: 22px;
    }
}
.old_offline_education_pack {
    display: grid;
    justify-items: center;
    margin: 140px auto 0;
    ._title_ {
        font-size: 24px;
        line-height: 34px;
    }
}
.time_line_block {
    justify-self: start;
    width: 100%;
    margin: 100px 0 0 0;
}
.time_line_block_types {
    margin-top: 44px;
    justify-items: center;
    > li {
        position: relative;
        font-weight: 500;
        font-size: 18px;
        line-height: 30px;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #000000;
        ul {
            padding: 0;
        }
    }
    > li::before {
        content: "";
        display: block;
        position: absolute;
        width: 30px;
        height: 30px;
        border: 2px solid #61b5a9;
        box-sizing: border-box;
        border-radius: 50%;
        top: -60px;
        left: 40px;
    }
}
.li_active_checked::before {
    background: #689c9a;
    filter: blur(5px);
}
.old_offline_education_pack_text {
    margin-left: -100px;
}
.time_line_block_types {
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    ul {
        list-style: none;
    }
    > li {
        padding: 5px 10px 100px;
    }
    > li:nth-child(3) {
        .hide_content {
            li {
                justify-self: center;
                align-self: center;
                grid-row: 1/4;
            }
        }
    }
    > li:nth-child(1) {
        .hide_content {
            padding: 20px;
        }
    }
}
.hide_content {
    background: #ffeca0;
    border-radius: 20px;
    position: relative;
    width: 234px;
    display: grid;
    opacity: 0;
    visibility: hidden;
    // padding: 20px 0 40px 20px !important;
    box-sizing: border-box;
    height: 126px;
    position: absolute;
    top: 100px;
    left: -40px;
    grid-template-rows: repeat(3, 1fr);
    align-items: center;
    transition: all 0.5s ease-in-out;
    input {
        display: none;
    }
    input:checked + .b_radio::after {
        display: inline-block;
    }
    .b_radio::after {
        left: -16px;
        top: 9px;
    }
    > li {
        height: 20px;
    }
    .b_radio {
        display: inline-block;
        margin-left: 30px;
    }
}
.hover:hover {
    > .hide_content {
        opacity: 1;
        visibility: visible;
    }
}
.hide_content::after {
    content: "";
    display: block;
    width: 40px;
    height: 40px;
    background: #ffeca0;
    transform: rotate(45deg);
    position: absolute;
    left: 40%;
    top: -20px;
    z-index: -1;
}

.what_will_we_do {
    ._title_ {
        width: max-content;
        margin: 0 auto 50px;
        .line {
            left: 0;
        }
    }
}
