@media all and (min-width: 800px) and (max-width: 1000px) {
    .wrapper {
        width: 100%;
    }
    .container {
        width: 100%;
    }
    .header_container {
        grid-template-columns: repeat(3, 1fr);
    }
    .nav {
        margin: 0;
        align-items: center;
        grid-column: 2/4;
        justify-content: center;
        > li {
            padding-top: 15px;
            padding: 15px 10px 25px;
        }
    }
    .nav_content {
        left: 0;
        top: 50px;
    }
    .main_title {
        .main_title_text_content {
            p {
                font-size: 30px;
            }
        }
    }
    .eductaion {
        grid-template-columns: repeat(3, 1fr);
    }
    .why_school {
        width: 100%;
        grid-template-columns: repeat(1, 1fr);
        p {
            font-size: 18px;
        }
        .right_side {
            margin-top: 50px;
            justify-self: center;
        }
    }
    .form {
        width: 100%;
    }
    .form_left_side_container {
        margin: 60px 20px 0 !important;
    }
    .eductaion_for {
        width: 100%;
    }
    .eductaion_for {
        > .for_children:nth-child(1) {
            img {
                margin-left: -120px;
            }
        }
    }
    .for_children {
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
        ._content {
            justify-self: center;
        }
    }
    .smart {
        width: 100%;
        ._content_title {
            max-width: 800px;
            .title_content {
                justify-self: center;
            }
            .line {
                width: 630px;
                margin-left: 95px;
            }
        }
    }
    .smart_text {
        width: 100% !important;
        text-align: center;
        font-size: 20px !important;
    }
    .example_block {
        justify-items: center;
        width: 100%;
        svg {
            justify-self: left;
        }
    }
    .time_line_history {
        width: 100%;
    }
    .example_right_side {
        width: 100%;
        display: grid;
        justify-items: center;
        p {
            margin: 0 0px 0 10px;
        }
    }
    .footer {
        width: 100%;
    }
    .change_for_children_columns {
        grid-template-columns: repeat(2, 1fr) !important;
    }
    .what_lan,
    .education_pack,
    .results {
        width: 100% !important;
    }
    .what_lan_right_side {
        margin-left: 0 !important;
    }
    .education_pack_bottom {
        min-width: 0 !important;
        max-width: 700px !important;
        justify-self: center;
        > div {
            min-width: 0 !important;
        }
    }
    .footer_right_side,
    .footer_foot > a:nth-child(2) {
        margin-right: 20px;
    }
    .education_pack {
        min-width: 0;
    }
    .education_pack_block {
        width: 300px !important;
    }
    .e_pack_active {
        display: grid !important;
    }
    .children_offline_pack {
        width: 100% !important;
    }
    .offline_pack_active {
        display: flex !important;
    }
    .chiidren_offline_come {
        display: grid;
        ._title_ {
            justify-self: center;
        }
    }
    .chiidren_offline_come_blocks {
        justify-items: center;
    }
    .children_offline_pack_blocks {
        width: 700px !important;
    }
    .purpose {
        grid-template-columns: repeat(1, 1fr) !important;
        justify-items: center;
    }
    .offline_result_blocks {
        grid-template-columns: repeat(2, 1fr) !important;
        justify-items: center !important;
    }
    .offline_result_block {
        width: 237px !important;
    }
    .offline_result {
        ._title_ {
            margin: auto;
        }
    }

    .offline_result_block:nth-child(1) {
        justify-self: center !important;
    }
    .old_offline_all_task {
        padding: 100px 0 !important;
        img {
            justify-self: center !important;
        }
    }
}
@media all and (max-width: 1400px) {
    .offert_content,
    .offert_block,
    .offert_num,
    .offert_text {
        width: 100% !important;
    }
    .offert_content {
        div {
            width: 100% !important;
        }
    }
    .offert_main_content {
        img {
            width: 100%;
        }
    }
}
@media all and (max-width: 1260px) {
    .offert_left_side {
        grid-column: 1/3;
        p {
            text-align: center;
        }
    }
    .offert_main_content {
        img {
            display: none;
        }
    }
}
