.header_container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
}
.header_contact {
    grid-column: 4/6;
    justify-self: end;
    display: flex;
    justify-content: space-between;
}
.nav {
    grid-column: 2/4;
    margin-left: 90px;
    display: flex;
    justify-content: space-between;
    padding: 0;
    list-style: none;
    > li {
        position: relative;
        padding: 0px 20px 25px;
    }
    > li:hover > .nav_content {
        opacity: 1;
        visibility: visible;
    }
    ul {
        list-style: none;
    }
}
.nav_content {
    transition: all 0.5s ease-in-out;
    padding: 0;
    width: 190px;
    height: 85px;
    background: #ffeca0;
    border-radius: 10px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    justify-content: center;
    text-align: center;
    align-items: center;
    position: absolute;
    top: 40px;
    left: -30px;
    opacity: 0;
    visibility: hidden;
    li {
        a {
            font-weight: 500;
            font-size: 16px;
            line-height: 26px;
            letter-spacing: 0.02em;
            font-feature-settings: "pnum" on, "lnum" on;
            color: #000000;
            text-decoration: none;
        }
    }
}
.burger_menu {
    display: none;
}
