@media all and (min-width: 1250px) and (max-width: 1400px) {
    .wrapper {
        width: 100% !important;
    }
    .container {
        margin: 0 auto;
    }
    .footer {
        width: 100% !important;
    }

    .smart {
        ._content_title {
            .title_content {
                text-align: left !important;
            }
            .new_line {
                width: 620px;
                position: absolute;
                top: -10px;
            }
            .new_line:nth-child(3) {
            }
            .new_line:nth-child(4) {
                top: 60px;
                width: 140px !important;
            }
        }
    }
    .smart_text {
        text-align: left !important;
    }
    .eductaion {
        grid-template-columns: repeat(3, 1fr);
    }
    .main_title {
        grid-template-columns: repeat(1, 1fr) !important;
    }
    .example {
        img {
            justify-self: center;
        }
    }
    .example_right_side {
        justify-self: center;
    }
    .history {
        max-width: 900px;
        margin: 140px auto !important;
    }
    .footer {
        width: 100%;
    }
}
