@media all and (max-width: 800px) {
    .education_pack_bottom {
        min-width: 0 !important;
        width: 300px !important;
        justify-self: center;
        > div {
            min-width: 0 !important;
        }
    }
    .education_pack {
        min-width: 0;
    }
    .education_pack_block {
        width: 100% !important;
    }
    .e_pack_active {
        display: grid !important;
    }
    .wrapper {
        width: 100% !important;
        margin: 0 auto !important;
        // margin: -8px 0 0 -8px !important;
    }
    .container {
        width: 90% !important;
    }
    .header_container {
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
        width: 100%;
        top: 0px;
        .nav,
        .header_contact {
            display: none;
        }
        .icon {
            svg {
                width: 192px;
            }
        }
    }
    .burger_menu {
        display: block;
        justify-self: end;

        margin-right: 10px;
        position: relative;
        grid-row: 2/3;
        grid-column: 1/3;
        width: 100%;
        margin: 0;
        max-height: 0;
    }
    .burger_menu::after {
        position: absolute;
        top: -35px;
        right: 25px;
        // left: 300px;
        content: "";
        display: block;
        width: 20px;
        height: 6px;
        border-top: 1px solid black;
        border-bottom: 1px solid black;
        transition: all 0.5s ease-in-out;
    }
    .burger_menu::before {
        position: absolute;
        top: -20px;
        right: 25px;
        // left: 300px;
        content: "";
        display: block;
        width: 20px;
        height: 6px;
        border-top: 1px solid black;
        transition: all 0.5s ease-in-out;
    }
    .burger_menu_contact {
        display: grid !important;
        grid-template-columns: repeat(5, 1fr);
        width: 100%;
        margin: 0;
        svg {
            margin: 0;
        }
    }
    .burger_menu_content {
        margin-top: 20px;
        opacity: 0;
        visibility: hidden;
        transition: all 1s ease-in-out;
        transform: scaleY(0);
        transform-origin: 0 0;
        max-height: 0;
        overflow: hidden;
        * {
            display: grid;
            justify-content: center;
            text-align: center;
            margin-top: 20px;
        }
        .mail {
            margin-top: 12px;
        }
        .address {
            width: 160px;
            margin: 12px auto 0;
        }
        .burger_menu_content_href {
            font-weight: 500;
            font-size: 14px;
            line-height: 22px;
            text-align: center;
            letter-spacing: 0.02em;
            font-feature-settings: "pnum" on, "lnum" on;
            color: #000000;
        }
        p {
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            font-feature-settings: "pnum" on, "lnum" on;
            color: #000000;
        }
        .b_contact {
            font-weight: 300;
            font-size: 14px;
            line-height: 22px;
            letter-spacing: 0.02em;
            font-feature-settings: "pnum" on, "lnum" on;
            color: #363636;
        }
    }
    .header.header_active {
        background: #fffbed;
        border-radius: 0px 0px 70px 70px;
        padding-bottom: 60px;
        z-index: 1;
        .burger_menu {
            max-height: 1000px;
            .burger_menu_content {
                max-height: 1000px;
                opacity: 1;
                visibility: visible;
                transform: scaleY(100%);
            }
        }
        .burger_menu::after {
            transform: rotate(45deg);
            border-top: transparent;
        }
        .burger_menu::before {
            transform: rotate(-45deg);
            top: -30px;
        }
    }
    .example_block {
        grid-template-columns: repeat(1, 1fr);
        margin-top: 30px !important;
        img {
            width: 350px;
        }
        p {
            width: 90%;
        }
        svg {
            display: none;
        }
    }
    .example_right_side {
        justify-self: start;
    }
    .footer {
        width: 100%;
    }
    .footer_content {
        grid-template-columns: repeat(1, 1fr);
    }
    .footer_center {
        order: 0;
        .button {
            margin-top: 40px;
        }
    }
    .footer_left_side {
        order: 1;
        justify-self: center;
        margin-top: 50px;
    }
    .footer_title {
        font-size: 16px;
        line-height: 24px;
    }
    a {
        font-size: 14px;
        line-height: 22px;
    }
    .footer_right_side {
        order: 2;
        justify-self: center;
        justify-content: center;
        margin-top: 30px;
        width: 171px;
        p {
            text-align: center !important;
        }
        a {
            justify-self: center;
            text-align: center !important;
        }
    }
    .footer_foot {
        padding-bottom: 60px;
        > a {
            width: 150px;
            font-size: 12px !important;
            line-height: 24px !important;
        }
    }
    .footer_right_side {
        margin-left: 0;
    }
}

