.chiidren_offline_come {
    ._title_ {
        width: 385px;
        font-size: 24px;
        line-height: 34px;
        .line {
            width: 100%;
        }
    }
}
.chiidren_offline_come_blocks {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.chiidren_offline_come_block {
    display: flex;
    width: 392px;
}
.chiidren_offline_come_text {
    margin-left: 30px;
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
}
@media all and (max-width: 420px) {
    .result_block {
        width: 90%;
        margin: 20px auto;
    }
    ._title_ {
        font-size: 20px !important;
        line-height: 28px !important;
    }
    .chiidren_offline_come {
        width: 100%;
        ._title_ {
            width: 100%;
            .line {
                width: 90%;
            }
        }
    }
    .chiidren_offline_come_blocks {
        width: 100%;
        grid-template-columns: repeat(1, 1fr);
    }
    .chiidren_offline_come_block {
        width: 100%;
    }
    .chiidren_offline_come_text {
        font-size: 16px;
        line-height: 24px;
    }
    .children_offline {
        .what_lan {
            .what_lan_right_side {
                ._title_ {
                    margin-left: 20px;
                }
                .what_lan_blocks {
                    margin-left: 20px;
                    width: 90%;
                }
            }
        }
    }
    .children_offline_pack {
        width: 100% !important;
        margin: 100px auto 0;
        ._title_ {
            .line {
                left: 20% !important;
                width: 210px !important;
            }
        }
        ._title_,
        .children_offline_pack_inputs {
            width: 100% !important;
        }
        .children_offline_pack_blocks {
            > div {
                width: 100%;
            }
        }
    }
    .children_offline_pack_inputs {
        grid-template-columns: repeat(1, 1fr) !important;
        label {
            font-size: 12px;
            line-height: 20px;
            justify-self: center !important;
            margin-top: 15px;
        }
    }
    .education_pack_block {
        width: 300px !important;
        margin-right: 0 !important;
    }
    .offline_pack_active {
    }
}

.children_offline_pack {
    width: 1046px;
    margin: 140px auto 0;
    display: grid;
    ._title_ {
        justify-self: center;
        font-size: 24px;
        line-height: 34px;
        text-align: center;
        .line {
            left: 35px;
        }
    }
}
.children_offline_pack_inputs {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    input {
        display: none;
    }
    input[type="radio"]:checked + label::after {
        background: #236967;
    }
    > label:nth-child(2) {
        justify-self: end;
    }
    > label:nth-child(6) {
        justify-self: start;
    }
}
.children_offline_pack_blocks {
    margin-top: 40px;
    width: 100%;
    > div {
        display: none;
        grid-template-columns: repeat(3, 1fr);
        > div:not(:last-child) {
            margin-right: 70px;
        }
    }
    .offline_pack_active {
        display: grid;
    }
}
.education_pack_block {
    border-radius: 30px;
    img {
        border-top-left-radius: 30px;
        border-top-right-radius: 30px;
    }
}
