@import "fonts";
// @import "slick";
@import "header";
html {
    scroll-behavior: smooth;
}
body {
    font-family: "Raleway";
    position: relative;
}
a {
    text-decoration: none;
}
.wrapper {
    margin: 50px auto 0 auto;
    width: 1260px;
}
.main_title {
    display: flex;
}
.main_title_text_content {
    flex: 1 1 50%;
    p {
        font-family: "NEXT ART";
        font-style: normal;
        font-weight: 900;
        font-size: 50px;
        line-height: 66px;
        /* or 132% */

        letter-spacing: 0.02em;
        font-feature-settings: "kern" off;

        color: #000000;
    }
}
.button {
    display: inline-block;
    box-sizing: border-box;
    padding: 18px 38px;
    width: 230px;
    height: 54px;
    background: #236967;
    border-radius: 30px;
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 0.01em;
    color: #ffffff;
}
.eductaion {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}
.eductaion_circle {
    position: relative;

    img {
    }
    > p:nth-child(2) {
        bottom: 25px;
    }
    p {
        position: absolute;
        bottom: -15px;
        left: 90px;
        font-family: "NEXT ART";
        font-weight: 900;
        font-size: 24px;
        line-height: 34px;
        letter-spacing: 0.02em;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #000000;
        background: #fff;
        z-index: 1;
    }
}
.languages {
    margin-top: 70px;
    background: #fffbed;
    border-radius: 70px;
    height: 275px;
    display: grid;
    align-items: center;
}
.languages_container {
    width: 800px;
    margin: 0 auto;
}
.languages_title {
    font-family: "NEXT ART";
    font-weight: 900;
    font-size: 24px;
    line-height: 34px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #000000;
}
.lang {
    display: flex;
    justify-content: space-between;
    p {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 155px;
        font-weight: 500;
        font-size: 18px;
        line-height: 30px;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #363636;
    }
    > p:last-child {
        width: 180px;
    }
}
.why_school {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 140px auto 0;
    width: 1100px;
}
.left_side {
    width: 350px;
    justify-self: center;
}
.right_side {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 650px;
}
.left_side_title {
    position: relative;
}
.line {
    width: 95%;
    height: 10px;
    background: #ffeca0;
    display: inline-block;
}
.title_content {
    z-index: 1;
    position: absolute;
    font-family: "NEXT ART";
    font-weight: 900;
    font-size: 24px;
    line-height: 34px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #000000;
    display: inline-block;
    bottom: -5px;
}
.left_side_text {
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
    margin: 30px 0 0 0;
}
.right_side_block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 220px;
    p {
        height: 60px;
        font-weight: 500;
        font-size: 18px;
        line-height: 30px;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #363636;
        margin: 0;
    }
}
.left_row {
    width: 163px;
}
.last {
    height: 90px !important;
}
.right_row_1 {
    width: 253px;
}
.right_row_2 {
    width: 233px;
}
.right_row_3 {
    width: 270px;
}
.left_row_block {
    width: 243px;
}
.right_side_right_blocks {
    > div {
        width: 325px;
        > p {
            width: 250px;
        }
    }
}
.right_side_right_blocks,
.right_side_left_blocks {
    > div:not(:first-child) {
        margin-top: 20px;
    }
}
.form {
    width: 1046px;
    margin: 140px auto;
    background: #fffbed;
    border-radius: 70px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    .form_left_side {
        // width: 700px;
        grid-column: 1/4;
        .form_left_side_container {
            margin: 60px 0 0 100px;
            display: grid;
        }
        .form_title {
            font-family: "NEXT ART";
            font-style: normal;
            font-weight: 900;
            font-size: 24px;
            line-height: 34px;
            letter-spacing: 0.02em;
            font-feature-settings: "pnum" on, "lnum" on;
            color: #000000;
        }
    }
    .form_right_side {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        > svg:nth-child(2) {
            border-top-right-radius: 50%;
        }
        > svg:last-child {
            border-bottom-right-radius: 50%;
        }
    }
}
.change {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 450px;
    margin-left: 50px;
    // justify-self: center;
    margin-bottom: 30px;
    input[type="radio"] {
        display: none;
    }
    input:checked + label::before {
        background: #236967;
    }
    label {
        position: relative;
        font-weight: 500;
        font-size: 18px;
        line-height: 30px;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #000000;
    }
    label::before {
        content: "";
        position: absolute;
        display: block;
        width: 200px;
        height: 2px;
        background: #94c6c4;
        top: 120%;
        left: -15%;
    }
    > label:nth-child(4) {
        justify-self: start;
    }
}
.inputs {
    width: 420px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    select {
        grid-column: 1/3;
    }
    input[type="radio"] {
        display: none;
    }
    input[type="radio"]:checked + .label::after {
        display: block !important;
    }
    input,
    select {
        height: 50px;
        box-sizing: border-box;
        padding: 12px 20px;
        margin: 15px 0;
        border: none;
        background: #ffffff;
        border: 1px solid #ffeca0;
        border-radius: 10px;
    }
    input,
    select,
    input::placeholder,
    select::placeholder {
        font-weight: 300;
        font-size: 16px;
        line-height: 26px;
        letter-spacing: 0.02em;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #363636;
        font-family: "Raleway";
    }
    #_name {
        margin-right: 20px;
    }
}
.b_radio {
    position: relative;
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
    margin-left: 25px;
}
.b_radio::before {
    content: "";
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    border: 1px solid #236967;
    position: absolute;
    left: -20px;
    top: 20%;
}
.b_radio::after {
    content: "";
    display: none;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: #236967;
    position: absolute;
    left: -15px;
    top: 35%;
}
#_mail {
    grid-column: 1/3;
}
.eductaion_for {
    width: 1033px;
    margin: 0 auto;
    ._content {
        justify-self: end;
    }
}
.for_children {
    margin: 140px 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
._content_title {
    display: grid;
    .line {
        width: 150px;
    }
    .line_2 {
        width: 210px;
    }
}
.content_rows {
    justify-self: end;
    width: 413px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.content_row_title {
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #000000;
}
.content_row {
    ul {
        list-style-image: url("../img/галочка.png");
        padding: 0 0 0 20px;
        li {
            font-weight: 300;
            font-size: 16px;
            line-height: 26px;
            letter-spacing: 0.02em;
            font-feature-settings: "pnum" on, "lnum" on;
            color: #363636;
            margin-top: 10px;
        }
    }
}
.content_row:not(:last-child) {
    margin-right: 10px;
}
.for_old {
    ._content {
        justify-self: start;
    }
}

.smart {
    width: 874px;
    margin: 0 auto;
    ._content_title {
        .line {
            width: 70%;
        }
    }
    .smart_text {
        width: 427px;
        font-weight: 300;
        font-size: 16px;
        line-height: 26px;
        letter-spacing: 0.02em;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #363636;
        margin-top: 30px;
    }
}
.b_text {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    margin: 50px 0 100px 0;
}
.b_letter_block {
    position: relative;
    display: grid;
    align-items: center;
}
.b_letter {
    position: absolute;
    z-index: -1;
    font-family: "Jost";
    font-style: normal;
    font-weight: 900;
    font-size: 90px;
    line-height: 28px;
    letter-spacing: 0.5px;
    color: #ffeca0;
    text-transform: uppercase;
    left: 15px;
    top: 5px;
}
.gray_text {
    font-weight: 300;
    font-size: 16px;
    margin-bottom: -10px;
    line-height: 26px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #236967;
    display: block;
    width: 101px;
    text-align: center;
}
.b_letter_block_text {
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #000000;
    text-align: center;
    display: block;
    width: 101px;
}
.bigger {
    width: 140px;
}
.example {
    display: grid;
}
.example_block:nth-child(2) {
    margin-top: 80px;
}
.example_block {
    display: grid;
    margin-top: 30px;
    grid-template-columns: repeat(2, 1fr);
    svg {
        align-self: center;
    }
}
.example_block:nth-child(2n + 1) {
    svg {
        justify-self: center;
        margin-left: 150px;
    }
}
.example_text {
    margin-left: 20px;
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
    width: 325px;
}
.example_bigger {
    width: 430px;
}
.smaller {
    width: 213px;
}
.example_b_letter_text {
    position: relative;
    margin-left: 20px;
    > span:nth-child(1) {
        font-weight: 500;
        font-size: 18px;
        line-height: 30px;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #000000;
        width: 202px;
        display: block;
    }
    .b_letter {
        left: -20px;
        top: 0;
    }
}
.line_none {
    display: none;
}
.not_visible {
    display: none;
}

.history {
    ._content_title {
        width: 485px;
        margin-bottom: 60px;
        .line {
            width: 100%;
        }
    }
}
.time_line_history {
    width: 875px;
}
.years {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    justify-items: center;
    label {
        font-weight: 300;
        font-size: 16px;
        line-height: 26px;
        letter-spacing: 0.02em;
        font-feature-settings: "pnum" on, "lnum" on;
        color: #000000;
        position: relative;
    }
    label::before {
        content: "";
        display: block;
        position: absolute;
        width: 10px;
        height: 10px;
        border: 2px solid #61b5a9;
        box-sizing: border-box;
        border-radius: 50%;
        top: 30px;
        left: 10px;
    }
    input {
        display: none;
    }
    input[type="radio"]:checked + label::before {
        background: #236967;
        filter: blur(2px);
    }
}
.time_line {
    width: 100%;
    height: 2px;
    background: #61b5a9;
    margin-top: 10px;
}
.year_info_block {
    min-height: 350px;
    margin-top: 70px;
    > div {
        display: none;
        grid-template-columns: repeat(2, 1fr);
        align-items: center;
        width: 700px;
        margin: 0 auto;
        .info {
            display: grid;
            justify-content: center;
            > p:first-child {
                font-family: "NEXT ART";
                font-style: normal;
                font-weight: 900;
                font-size: 24px;
                line-height: 34px;
                letter-spacing: 0.02em;
                font-feature-settings: "pnum" on, "lnum" on;
                color: #000000;
                margin: 13px 0;
            }
        }
        .year_info_text {
            margin: 0;
            width: 250px;
            font-weight: 500;
            font-size: 18px;
            line-height: 30px;
            font-feature-settings: "pnum" on, "lnum" on;
            color: #363636;
        }
    }
}
.year_active {
    display: grid !important;
}

.button {
    border: none !important;
    cursor: pointer;
}
.footer_foot {
    a {
        color: #363636;
    }
}
@import "footer";
@import "adaptive";
@import "children_online";
@import "education_pack";
@import "children_offline";
@import "swiper";
@import "old_offline";
@import "offert";
