.offert_main_content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    img {
        justify-self: end;
    }
}
.offert_title {
    font-family: "NEXT ART";
    font-style: normal;
    font-weight: 900;
    font-size: 50px;
    line-height: 66px;
    letter-spacing: 0.02em;
    font-feature-settings: "kern" off;
    color: #000000;
    margin: 60px 0 10px 0;
}
.offert_pod_title {
    font-weight: 500;
    font-size: 22px;
    line-height: 30px;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
    margin: 0 0 60px 0;
}
.redaction {
    font-family: "NEXT ART";
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 34px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #000000;
    margin: 0 0 10px 0;
}
.redaction_date {
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
    margin: 0 0 60px 0;
}
.offert_info {
    font-weight: normal;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
}
.offert_num {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #000000;
    margin: 0 0 30px 0;
}
.offert_text {
    font-weight: 300;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.02em;
    font-feature-settings: "pnum" on, "lnum" on;
    color: #363636;
}
.offert_block {
    width: 620px;
}
.offert_content {
    margin: 100px 0 140px 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    > div {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
    }
}
@media all and (max-width: 420px) {
    .offert_content {
        grid-template-columns: repeat(1, 1fr);
    }
    .offert_block {
        width: 100%;
    }
    .offert_num {
        font-size: 16px;
        line-height: 24px;
    }
    .offert_text {
        font-size: 14px;
        line-height: 22px;
    }
    .offert_main_content {
        grid-template-columns: repeat(1, 1fr);
        img {
            width: 100%;
        }
    }
    .offert_title {
        font-size: 30px;
        line-height: 40px;
    }
    .offert_pod_title {
        font-size: 20px;
        line-height: 30px;
    }
    .redaction {
        font-size: 20px;
        line-height: 28px;
    }
    .redaction_date {
        font-size: 16px;
        line-height: 24px;
    }
    .offert_info {
        font-size: 14px;
        line-height: 22px;
    }
}
