@media all and (max-width: 340px) {
    .history {
        img {
            width: 100%;
        }
    }
    .smart {
        img {
            width: 100%;
        }
    }
    .for_children {
        img {
            width: 100%;
        }
    }
    .right_side_left_blocks {
        width: 100%;
        > div {
        }
    }
    .smart_text {
        width: 100% !important;
    }
    .right_side_right_blocks {
        width: 100%;
        > div {
            width: 100%;
        }
    }
    .form {
        .change {
            grid-template-columns: repeat(1, 1fr);
            > label {
                margin-top: 20px;
                justify-self: center;
            }
            > label::before {
                width: 130% !important;
                left: -15% !important;
            }
        }
        .inputs {
            margin-left: -20px;
            align-self: center;
            justify-self: center;
            select {
                margin: 30px 0 30px 0px;
            }
            input,
            select {
                margin-left: 10px;
            }
            .b_radio {
                grid-column: 1/3;
                justify-self: center;
                margin-top: 15px;
            }
        }
    }
    .form_title {
        justify-self: center;
    }
    .why_school {
        .left_side_title {
            height: 30px;
            .line {
                top: -10px;
                position: absolute;
                width: 180px;
            }
            .not_visible {
                display: inline-block;
                top: 15px;
                width: 100px;
            }
        }
    }
    .change_for_children_column {
        width: 100% !important;
        span {
            width: 100% !important;
        }
    }
    .what_lan {
        img {
            width: 100% !important;
        }
    }
    .what_lan_right_side {
        width: 100% !important;
        ._title_ {
            width: 100% !important;
            .line {
                display: none;
            }
        }
    }
    .what_lan_blocks {
        width: 100% !important;
        grid-template-columns: repeat(1, 1fr) !important;
    }
    .long {
        grid-column: 1/2 !important;
    }
    .e_pack > label:nth-child(4) {
        margin-left: 10px !important;
    }
    .e_pack {
        label::after {
            width: 80% !important;
            left: 10% !important;
        }
    }
    .results {
        ._title_ {
            width: 100% !important;
        }
    }
    .chiidren_offline_come {
        .line {
            display: none;
        }
    }
    .what_lan_right_side {
        ._title_ {
            width: 90% !important;
        }
    }
    .what_lan_blocks {
        width: 90% !important;
    }
    .eductaion_circle {
        p {
            left: 30% !important;
        }
    }
    .eductaion_circle:nth-child(2) {
        p {
            font-size: 22px !important;
        }
    }
    .purpose {
        img {
            width: 100% !important;
        }
        ._title_ {
            width: 100% !important;
            font-size: 16px !important;
            .line:nth-child(3) {
                width: 70% !important;
            }
        }
    }
    .purpose_text {
        width: 100% !important;
    }
    .all_task_left_side {
        width: 100% !important;
        ._title_ {
            font-size: 16px !important;
        }
    }
    .old_offline_all_task {
        display: block !important;
    }
    .all_task_text {
        width: 100% !important;
    }
}
